import React from 'react'
import { connect } from 'react-redux'
import OnorLogo from "../../images/onorlogo.png"
import { Link, withRouter } from 'react-router-dom'
import { Col, Navbar, Nav, Row } from 'react-bootstrap'
import jwt from 'jsonwebtoken'
import _, { isUndefined } from 'lodash'
import {
    action_progress_start,
    action_progress_stop,
} from "../../redux/actions/progressAction"
import {
    action_sign_out,
    action_sign_out_seller,
    thunk_seller_sign_in,
} from "../../redux/actions/authActions"
import { action_sign_up_as_seller } from "../../redux/actions/authActions"
import { action_dialog_open } from "../../redux/actions/dialogAction"
import freeConsultation from '../../images/free-consultation.png'

const NavComponent = (props) => {
    const handleSignInAsSeller = async () => {
        try {
            if (props.auth.isAuthorized) {
                const { userEmail, userPassword } = jwt.decode(
                    localStorage.getItem("jwtToken")
                );
                const payload = {
                    email: userEmail,
                    password: userPassword,
                };
                props.actionStartProgress({
                    isOpen: 1,
                });
                const result = await props.thunkActionSignInAsSeller(payload);
                props.actionDialogOpen({
                    title: `Success`,
                    message: `You've successfully switched to seller`,
                    positive: "Ok",
                    type: "Alert",
                });
                props.history.push("/");
                localStorage.setItem("sellerJwtToken", result.data.token);
                props.signUpAsSeller();
                props.progressStop();

                props.history.push("/");
            }
        } catch (e) {
            props.actionStopProgress({
                isOpen: false,
            });
            if (
                !isUndefined(e) &&
                !isUndefined(e.response) &&
                !isUndefined(e.response.status)
            ) {
                if (e.response.status === 404) {
                    // props.actionDialogOpen({
                    //   title: `You are not registered`,
                    //   message: `You are not registered as merchant. Please register yourself`,
                    //   positive: "Ok",
                    //   type: "Alert",
                    // });
                    props.history.push("/seller/");
                }
            }
        }
    }
    return (
        <>
            <Navbar className={'mt-2 ml-lg-5'} collapseOnSelect expand="lg" >
                <Navbar.Brand className='logo-app' >
                    <div className="container-fluid">
                        <img
                            src={OnorLogo}
                            width='69'
                            height='35'
                            className='d-inline-block align-top'
                            alt='logo'
                        />
                    </div>
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" className="navbar-toggler-css" />
                <Navbar.Collapse id="responsive-navbar-nav" className="justify-content-end">
                    <Nav >
                        {
                            props.auth.isAuthorized
                            &&
                            <Nav.Item className={'px-4'}>
                                <Nav.Link className={'text-dark'} onClick={(e) => window.location.assign('https://calendly.com/d/dd7m-zwhj/consult-a-makeup-maestro', '_blank')}><img src={freeConsultation} style={{ height: '65px', width: '180px' }} className={'m-0 p-0'} alt="free-consultation" /></Nav.Link>
                            </Nav.Item>
                        }
                        <Nav.Item className={'px-4'}>
                            <Nav.Link className={'text-dark'} onClick={(e) => props.history.push('/')}>Home</Nav.Link>
                        </Nav.Item>
                        <Nav.Item className={'px-3'}>
                            <Nav.Link className={'text-dark'} onClick={(e) => props.history.push('/messages')}>Message</Nav.Link>
                        </Nav.Item>
                        <Nav.Item className={'px-3'}>
                            <Nav.Link className={'text-dark'} onClick={(e) => props.history.push('/user/buyer/edit/')}>Profile</Nav.Link>
                        </Nav.Item>
                        <Nav.Item className={'px-3'}>
                            <Nav.Link className={'text-dark'} onClick={(e) => props.history.push('/about')}>About Onor</Nav.Link>
                        </Nav.Item>
                        {
                            props.auth.isAuthorized ?
                                props.auth.isAlsoSeller ?
                                    <>
                                        <Nav.Item className={'px-3'}>
                                            <Nav.Link className={'onor_btn rounded'} onClick={(e) => props.history.push('/seller/create/summary')}>Post a Session</Nav.Link>
                                        </Nav.Item>
                                        <Nav.Item className={'px-3'}>
                                            <Nav.Link className={'onor_secondery_btn rounded'} onClick={(e) => {
                                                props.history.push("/");
                                                props.actionSignOutAsSeller();
                                                props.actionDialogOpen({
                                                    title: `Success`,
                                                    message: `You've successfully switched to Buyer`,
                                                    positive: "Ok",
                                                    type: "Alert",
                                                });
                                                props.history.push("/");
                                            }}>Switch to Buyer</Nav.Link>
                                        </Nav.Item>
                                    </>
                                    :
                                    <>
                                        <Nav.Item className={'px-3'}>
                                            <Nav.Link className={'onor_btn rounded'} onClick={(e) => props.history.push('/buyer/postrequest')}>Post a Request</Nav.Link>
                                        </Nav.Item>
                                        <Nav.Item className={'px-3'}>
                                            <Nav.Link className={'onor_secondery_btn rounded'}
                                                onClick={(e) => {
                                                    handleSignInAsSeller()
                                                }}>Switch to Seller</Nav.Link>
                                        </Nav.Item>
                                    </>
                                :
                                <>
                                    <Nav.Item className={'px-3'}>
                                        <Nav.Link className={'text-dark'} onClick={(e) => props.history.push('/signin')}>Login</Nav.Link>
                                    </Nav.Item>
                                    <Nav.Item className={'px-3'}>
                                        <Nav.Link className={'onor_secondery_btn rounded'} onClick={(e) => props.history.push('/signup')} style={{ padding: '7px 20px' }}>Sign up</Nav.Link>
                                    </Nav.Item>
                                </>
                        }
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        </>
    )
}

const mapStateToProps = (state) => {
    return {
        auth: state.auth,
        dialog: state.dialog,
        searchGig: state.searchGig,
        progress: state.progress
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        signUpAsSeller: () => dispatch(action_sign_up_as_seller()),
        actionSignOut: () => dispatch(action_sign_out()),
        actionDialogOpen: (payload) => dispatch(action_dialog_open(payload)),
        actionSignOutAsSeller: () => dispatch(action_sign_out_seller()),
        thunkActionSignInAsSeller: (payload) =>
            dispatch(thunk_seller_sign_in(payload)),
        actionStartProgress: (payload) => dispatch(action_progress_start(payload)),
        actionStopProgress: (payload) => dispatch(action_progress_stop(payload)),
    };
};
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(NavComponent))